import { useStaticQuery, graphql } from 'gatsby';
import { GroupItem, Post } from 'models/post';

interface QueryResult {
  allMarkdownRemark: {
    group: Array<GroupItem<Post>>;
  };
}
export const useRelatedProjectPosts = () => {
  const data: QueryResult = useStaticQuery(graphql`
    query {
      allMarkdownRemark {
        group(field: frontmatter___relatedProject) {
          fieldValue
          totalCount
          nodes {
            fields {
              slug
              path
              lang
            }
            frontmatter {
              title
              date
              categories
              thumbnail {
                childImageSharp {
                  fixed(width: 320, height: 320, quality: 100, cropFocus: CENTER) {
                    src
                  }
                }
              }
            }
          }
        }
      }
    }
  `);

  return data;
};
