import { useStaticQuery, graphql } from 'gatsby';
import { FluidObject } from 'gatsby-image';
import { useProfileInfo } from './useProfileInfo';
import { useRelatedProjectPosts } from './useRelatedProjectPosts';

type AboutImagesKey = 'simpleAnn' | 'simpleWaveform' | 'solarSystem' | 'vueWithCoffee' | 'zarbis';

type AboutImagesQueryResult = {
  [key in AboutImagesKey]: {
    childImageSharp: {
      fluid: FluidObject;
    };
  };
};

export const useAboutData = () => {
  const profile = useProfileInfo();
  const relatedPosts = useRelatedProjectPosts();
  const images = useStaticQuery<AboutImagesQueryResult>(graphql`
    query AboutImagesQuery {
      simpleAnn: file(absolutePath: { regex: "/simple-ann.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      simpleWaveform: file(absolutePath: { regex: "/simple-waveform.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      solarSystem: file(absolutePath: { regex: "/solar-system.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      vueWithCoffee: file(absolutePath: { regex: "/vue-with-coffee/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      zarbis: file(absolutePath: { regex: "/zarbis/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return {
    ...profile,
    ...relatedPosts,
    ...images,
  };
};
