import React, { ReactNode, useCallback } from 'react';
import { Box, Typography, Button, Grid, makeStyles, Paper, Divider, Theme } from '@material-ui/core';
import * as Icon from 'react-feather';
import SimplePostItem from 'components/Items/SimplePostItem';
import { Post } from 'models/post';
import Image, { FluidObject } from 'gatsby-image';
import { Logger } from 'utils/loggers';

const useStyles = makeStyles<Theme>(({ shape, shadows, palette, spacing }) => ({
  thumbnail: {
    borderRadius: shape.borderRadius,
    marginBottom: spacing(2),
    boxShadow: shadows[3],
  },
  buttonWrapper: {
    marginBottom: spacing(4),
  },
  button: {
    backgroundColor: palette.grey['900'],
    color: palette.grey['100'],
    width: '100%',
    '&:hover': {
      backgroundColor: palette.grey['700'],
      color: palette.grey['100'],
    },
  },
  relatedPostsWrapper: {
    marginTop: spacing(2),
  },
  relatedPosts: {
    padding: spacing(2),
  },
  divider: {
    margin: `${spacing(4)}px 0`,
  },
}));

interface Props {
  title: string;
  children: ReactNode;
  thumbnail: FluidObject;
  githubLink?: string;
  demoLink?: string;
  posts?: Post[];
  divider?: boolean;
  logger?: Logger;
}
const ProjectIntroduction = ({
  title,
  children,
  thumbnail,
  githubLink,
  demoLink,
  posts = [],
  divider = true,
  logger,
}: Props) => {
  const styles = useStyles();

  const handleGithubClick = useCallback(() => {
    logger?.click(`click_github_link`, {
      product: title,
    });
  }, [logger, title]);
  const handleDemoClick = useCallback(() => {
    logger?.click(`click_demo_link`, {
      product: title,
    });
  }, [logger, title]);
  const handlePostClick = useCallback(
    (path: string) => {
      logger?.click('click_related_posting', {
        path,
      });
    },
    [logger]
  );

  return (
    <Box>
      <Typography variant="h6" component="h3" gutterBottom>
        # {title}
      </Typography>
      <Image fluid={thumbnail} className={styles.thumbnail} />
      <Grid spacing={1} container justify="center" className={styles.buttonWrapper}>
        {githubLink && (
          <Grid item>
            <Button href={githubLink} className={styles.button} onClick={handleGithubClick} target="_blank">
              <Icon.GitHub />
            </Button>
          </Grid>
        )}
        {demoLink && (
          <Grid item>
            <Button href={demoLink} className={styles.button} onClick={handleDemoClick} target="_blank">
              <Icon.Play />
            </Button>
          </Grid>
        )}
      </Grid>
      {children}
      <Box className={styles.relatedPostsWrapper}>
        <Paper className={styles.relatedPosts} variant="outlined">
          <Typography variant="h6" component="h4" gutterBottom>
            ê´€ë ¨ í¬ìŠ¤íŒ…
          </Typography>
          {posts.map(({ frontmatter, fields }, index) => (
            <SimplePostItem
              key={fields.path ?? index}
              thumbnail={frontmatter.thumbnail.childImageSharp?.fixed?.src ?? ''}
              title={frontmatter.title ?? ''}
              date={frontmatter.date}
              path={fields.path ?? '/'}
              categories={frontmatter.categories}
              onClick={handlePostClick}
            />
          ))}
        </Paper>
      </Box>
      {divider && <Divider className={styles.divider} />}
    </Box>
  );
};

export default ProjectIntroduction;
